.category {
	width:93%;
	margin:0 auto;
	text-align:center;
	padding: 50px 0;
	h1 {
		text-transform:uppercase;
		font-size:36px;
		font-weight:400;
	}

	.show {
		line-height:60px;
		@include secondary-font;
		color:#606060;
		font-size:18px;
		position:relative;

		&:after {
			content: '▶';
			font-size:6px;
			position:absolute;
			left: 100px;
			font-style:normal;

		}

	}

	ul {
		font-size:0;
		li {
			display:inline-block;
			width:25%;
			font-size:14px;

			@include mq(768px,1140px) {
				width:50%;
			}

			@include mq(0,767px) {
				width:100%;
			}


			a {
				text-decoration:none;
				color:#303030;
				position:relative;

				figure {
					width:200px;
					height:256px;
					margin:30px auto;
					border: 1px solid #e5e5e5;
					overflow:hidden;
					position:relative;

					img {
						height:100%;
						position: absolute;
					    left: 50%;
					    transform: translateX(-50%);

					}
				}

				h3 {
					font-weight:400;
				}

				p {
					line-height:32px;
					font-weight:300;
					display:inline-block;
					position:relative;

					&:before {
						content:'€';
						left:-12px;
						position:absolute;
					}
				}
			}
		}
	}
}
