.breadcrumbs {
	line-height:65px;
	width:93%;
	text-transform: uppercase;
	margin: 0 auto;
	font-size:12px;
	color: #606060;
	@include mq(0,670px) {
		line-height:12px;
		margin: 20px auto;
	}
	ul {
		li {
			display: inline-block;
			margin-right: 30px;

			@include mq(0,670px) {
				margin-right:15px;
			}

			&:after {
				content:"/";
				position: relative;
				left:15px;

				@include mq(0,670px) {
					left:7.5px;
				}
			}

			&:last-child {
				text-decoration:underline;
				&:after {
					content:"";

				}
			}

			a{
				text-decoration: none;
				color: #606060;
			}
		}
	}
}