.gallery {
	width:93%;
	height:43.5vw;
	max-height:590px;
	margin: 0 auto;
	position: relative;
	background-repeat:no-repeat;
    background-size:cover;
    background-position:center;

	@include mq(0,670px) {
		height:50vw;
	}
	ul {
		position: absolute;
		right:11px;
		bottom:0;
		li {
			margin-bottom:11px;
			background-color: #f7f7f7;
			width:70px;
			height:70px;
			border: #e5e5e5 solid 1px;
			overflow:hidden;
			position:relative;

			@include mq(0,670px) {
				display:none;
			}

			img {
				height:100%;
				position: absolute;
				left: 50%;
				transform: translateX(-50%);
			}

		}
	}
}