.hero {
	height: 540px;
	width: 100%;
	text-align: center;

	@media only screen and (max-width: 670px) {
		height:540px;
	}

	article {
		width:80%;
		position: relative;
    	top:50%;
    	left:50%;
    	transform: translate(-50%,-50%);
		h1 {
		text-transform: uppercase;
		font-size: 72px;
		letter-spacing: 2px;

			@media only screen and (max-width: 670px) {
			font-size:50px;
			margin-bottom:10px;
			}
		}

		p {
		font-size: 24px;
		line-height: 58px;
		color: #606060;
		@include secondary-font;

			@media only screen and (max-width: 670px) {
			font-size:18px;
			line-height: 30px;
			}
		}

		a {
		margin-top: 70px;
		display: inline-block;
		border: 1px solid #282828;
		padding: 0 33px;
		text-decoration: none;
		line-height: 58px;
		color: #282828;
		text-transform: uppercase;
		letter-spacing: 1px;
		font-size: 14px;

			@media only screen and (max-width: 670px) {
				margin-top:50px;
			}
		}
	}	
}