.tiles {
	letter-spacing: 1.5px;
	font-size: 0;
	width: 93%;
	margin: 0 auto;
	overflow: hidden;
	.rectangle {
		height: 600px;
		width: calc(100% / 4);

		@include mq(0,670px) {
			width:100%;
			height:300px;
		}

		@include mq(670px,975px) {
			width:50%;
		}

	}

	article {
		width: calc(100% / 4);
		height: 300px;
		float:left;
		font-size:10px;
		color: #ffffff;
		position: relative;
		text-align: center;

		@include mq(0,670px) {
			width:100%;
		}

		@include mq(670px,975px) {
			width:50%;
		}

		div {
			text-align: center;
			position: relative;
			top: 50%;
			transform: translateY(-50%);
			width: 100%;
			max-width: 60%;
			margin: 0 auto;
		}

		.info {
			text-transform: uppercase;
			line-height: 27px;
			h1 {
				font-size:24px;
			}

			h2 {
				@include secondary-font;
				font-size: 17px;
			}

			i {
				font-size:40px;
			}
		}

		.social {
			i {
				font-size: 50px;
			}

			em {
				font-size:13px;
				@include secondary-font;
				line-height: 20px;
				display: block;
				margin-top: 28px;
			}

			a {
				display: block;
				margin-top: 23px;
				font-size: 13px;
				text-decoration: none;
				color: #ffffff;
			}

			&:before {
				content: '';
				width: 15%;
				height: 1px;
				background:#ffffff;
				position: absolute;
				top: 26px;
				left: 15%;
			}

			&:after {
				content: '';
				width: 15%;
				height: 1px;
				background:#ffffff;
				position: absolute;
				top: 26px;
				right: 15%;
			}
		}

		&:nth-of-type(1){
			background: #d2d2d2;
		}

		&:nth-of-type(2){
			background: #bbbbbb;
		}

		&:nth-of-type(3){
			background: #dbdbdb;
		}

		&:nth-of-type(4){
			background: #606060;
		}

		&:nth-of-type(5){
			background: #cacaca;
		}

		&:nth-of-type(6){
			background: #bbbbbb;
		}

		&:nth-of-type(7){
			background: #d7d7d7;
			float:right;
		}

		&:nth-of-type(8){
			background: #c2c2c2;
		}

		&:nth-of-type(9){
			background: #606060;
		}

		&:nth-of-type(10){
			background: #d0d0d0;
		}

	}
}