.slider {
	width: 93%;
	margin: 50px auto;

	h1 {
		@include secondary-font;
		font-size:24px;
		text-align:center;
		margin: 42px 0 57px 0;
		color: #606060;
	}
	div {
		background: #bbbbbb;
		height: 116px;
		color:#ffffff;
		width:100%;

		i {
			display:inline-block;
			background-color:#e4e4e4;
			line-height:116px;
			width:40px;
			text-align:center;
			font-size:25px;

			@include mq(0,670px) {
				background-color:#bbbbbb;
				font-size:17px;
				width:20px;
				opacity:0.7;
			}

			&.icon-arrow-left-thick {
			}

			&.icon-arrow-right-thick {
				float:right;
			}

		}
	}

	a {
		width:100%;
		text-transform:uppercase;
		text-decoration:none;
		font-weight:300;
		line-height:44px;
		background-color:#f1f1f1;
		text-align:center;
		display:inline-block;
		color: #606060;
		margin: 35px 0 0 0;
	}

	hr {
		color: #e5e5e5;
		margin-bottom:46px;
	}
}